<template>
  <div class="outbox">
  <div class="sec-border"></div>
  <div class="first-border"></div>
  <div id="main" class="main text-white">
      <ul>
      <li><input v-model="username" type="text" placeholder="Username"></li>
      <li><input v-model="password" type="password" placeholder="Password"></li>
      <li><button @click="login">LOGIN</button></li>
    </ul>
  </div>
</div>
</template>

<script>
// @ is an alias to /src

import axios from 'axios';
export default {
  name: 'LoginView',
  data(){
    return{
      username:'',
      password:''
    }
  },
  methods:{
    async login(){
      let data ={
        username:this.username,
        password:this.password
      }
      try{
        axios.request({
          method:'post',
          data:data,
          url:'/api/login'
        })
        this.getLoginInfo()
        
      }catch(err){
        if(err == undefined){
          return
        }
        
      }
    },
    async getLoginInfo(){
      try{
        let response = await axios.request({
          method:"post",
          url:'/api/get_login_info'
        })
        console.log(response)
        if(response.data.login == '1'){
          alert("登录成功")
          let data ={
            username:this.username
          }
          window.localStorage.setItem("username",this.username)
          alert("login Success :"+data.username)
          this.$router.push('/about')   
        }else{
          alert("用户名或者密码错误")
        }
      }catch(err){
        return
      }
    }
  }
}
</script>
